<template>
    <div class="container-chat">
        <div class="autorize">
            <div class="form_block">
                <form @submit.prevent="restore">
                <h1>Восстановление пароля</h1>
                <p v-if="serverErrr" class="error">{{ errData }}</p>
                <div class="input_block">
                    <input required v-model="username" type="username" placeholder="имя пользователя"/>
                </div>
                <div v-if="checkPass" class="new_pass">
                    <span>Новый пароль:</span><p>{{ password }}</p>
                </div>
                <div class="button_block">
                    <button type="submit" :disabled="!validate" :class="validate ? 'activ' : 'non_activ'" v-on:click="restore">Отправить</button>
                    <span><RouterLink to="login">На страницу авторизации</RouterLink></span>
                </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>

import { post } from '@/api/netork';

export default{
    data(){
        return{
            username:"",
            password:"",
            serverErrr: false,
            errData:"",
        }
    },
    methods:{
        restore(){
            let parameters = {'username':this.username};
            post('/restore', parameters)
                .then((resp) => {
                    this.password = resp.data.password
                })
                .catch((err) => {
                    this.serverErrr = true
                    this.errData = err.response.data
                }) 
        }
    },
    computed:{
        validate(){
            if (this.username.length == 0){
                return false
            }
            return true
        },
        checkPass(){
            if (this.password.length == 0){
                return false
            }
            return true
        }
    }
}

</script>


<style scoped>
.new_pass {
    display: flex;
    justify-content: center;
    align-items: center;
}
.new_pass p{
    font-size: 18px;
    font-weight: bold;
    color: #e2b49e;
    margin-left: 10px;
}
</style>
